.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#root {
        position: absolute;
    height: 100vh;
    top: 0;
    /*background: red;*/
    z-index: 9999999;
    width: 100%;
}

.btn-close {
    cursor: pointer;
    position: absolute;
    right: 10px;
    top: 10px;
    margin: 3px;
    width: 20px;
    height: 20px;
    float: right;
}

.clear {
    clear: both;
}

.btn-close::before,
.btn-close::after {
    content: "";
    height: 2px;
    display: block;
    background: #d6d6d6;
}

.btn-close::before {
    margin-top: 10px;
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg);
}

.btn-close::after {
    margin-top: -2px;
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg);
}

.btn-close:hover:after, .btn-close:hover:before {
    background: #4A9EA1;
}


.modal-wrapper {
    display: flex;
    z-index: 999;
    width: 100%;
    height: 100%;
    position: fixed;
    visibility: hidden;
    top: 0;
    left: 0;
    background: rgba(25, 18, 12, 0.3);
    opacity: 0;
    filter: alpha(opacity=0);
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}
.modal-wrapper.open {
    visibility: visible;
    opacity: 1;
    filter: alpha(opacity=1);
}

.modal {
    padding-left: 15px;
    padding-right: 15px;
    display: inline-block;
    height: auto;
    width: auto;
    position: relative;
    margin: auto;
    background: #FFF;
    box-shadow: 0px 16px 16px -6px rgba(47, 46, 38, 0.5);
    opacity: 0;
    filter: alpha(opacity=0);
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    -ms-transform: scale(0.7);
    /* IE 9 */
    -webkit-transform: scale(0.7);
    /* Safari */
    transform: scale(0.7);
    border-radius: 5px;

    font-family: 'Inter';
}

.modal-wrapper.open .modal {
    opacity: 1;
    filter: alpha(opacity=100);
    -ms-transform: scale(1);
    /* IE 9 */
    -webkit-transform: scale(1);
    /* Safari */
    transform: scale(1);
    padding: 20px 100px;
    padding-bottom: 50px;
}

.content {
    text-align: center;
}

@media screen and (max-width: 425px) {
    .modal {
        width: 100%;
        height: 70%;
        padding: 0;
    }

    .btn-close {
        right: 5px;
    }
}

.modal .ipfs-form-control {
    width: 450px;
    text-align: left;
    color: #595959;
    margin-bottom: 35px;
}
.modal .ipfs-form-control input[type="text"] {
    width: 100%;
    max-width: 100%;
}

.modal .ipfs-form-control h4 {
    font-size: 16px;
}

.modal .ipfs-form-control span {
    display: block;
    font-size: 12px;
    margin-bottom: 10px;
    text-transform: none;
}

.modal .title {
    color: #4A9EA1;
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 75px;
}

.modal .ipfs-button {
    margin-top: 0;
}